@import url(https://fonts.googleapis.com/css?family=Bungee+Inline|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background: url(/static/media/cork7.0c65e238.jpg) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  overflow: auto;
}

.header-title {
  font-size: 6vw;
  font-family: 'Bungee Inline', cursive;
  font-weight: 400;
}

.App-header {
  margin: auto 0;
  text-align: center;
  height: 130px;
  padding: 10px;
}
.auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.log-title {
  font-size: 4vh;
  font-family: 'Bungee Inline', cursive;
}

input[type=text] {
  width: 100%;
  padding: 5px 10px;
  margin: 8px 0;
  box-sizing: border-box;
  box-shadow: 1px 1px black;
}

.post-it {
  display: inline-grid;
  height: 155px;
  width: 155px;
  margin: 5vw;
  background: rgb(247, 247, 18);
  font-size: 18px;
  text-align: center;
  box-shadow: 2px 2px;
}

.note-title {
  margin: auto 0;
}

.deleteButton {
  visibility: hidden;
  text-align: right;
  margin: 5px;
  font-size: 18px;
  cursor: pointer;
  color: rgb(163, 7, 7);
}

.post-it:hover .deleteButton {
  visibility: visible;
}



.Login {
  background:  rgb(94, 179, 218); 
  box-shadow:  1px 1px black;
  border-radius: 40%;
  font-size: 3vh;
  height: 6vh;
  margin: 1vw;
}

.register {
 color: rgb(53, 55, 201);
  border-radius: 40%;
  font-size: 4vh;
  font-weight: 500;
  height: 6vh;
  margin: 2vw;
}

.user-name {
  font-size: 4vh;
}

.logout {
  background-color:  rgb(94, 179, 218);
  border-radius: 40%;
  font-size: 4vh;
  font-weight: 400;
  height: 6vh;
  margin: 2vw;
}

.register:hover {
  color: rgb(56, 136, 56);
}




.newIdeaButton {
    display: inline-block;
    background:  rgb(94, 179, 218);
    color: white;
    border: none;
    font-size: 18px;
    cursor: pointer;
    margin-right: 10px;
    margin-left: 70px;
    padding:10px;
    box-shadow: 2px 2px black;
    border-radius: 40%;
  }

  button:hover {
    background-color: rgb(103, 209, 103);
  }

  .note-text {
    border: 0;
    background: none;
    outline: none;
    margin-top:10px;
    width: 140px;
    font-size: 11px;
  }
  
  .form-text:focus {
    border: solid 1px lightgrey;
  }
  
  textarea {
    resize: none;
    height: 90px;
    font-size: 11px;
  }
